.card {
  background: lightgray;
  border-radius: 10px;
  transition: transform 0.5s;
  padding: 1rem;
  box-shadow: 0px 5px 10px rgb(0, 0, 0);
  color: black;
  &:hover {
    transform: translate(0px, -5px);
    cursor: pointer;
  }
}

// overriding bootstrap css
.card-header {
  background: none !important;
  border: none !important;
}